import React from 'react'
import style from './MySkills.module.scss'
import Skill from "./Skill/Skill";
import {initialState} from "../initialState";
import {Header} from "../common/header/Header";
import commonStyle from "../common/styles/Container.module.scss"

const Skills = () => {
    const arrSkills = initialState.skills
    return (
        <section>
            <div className={commonStyle.wrapperBackground}>
                <article className={style.skillsContainer}>
                    <Header nav={".Главная"} nameBlock={".Скиллы"}/>
                    <div className={style.skills}>
                        {arrSkills.map(el => <Skill title={el.title} description={el.description}/>)}
                    </div>
                </article>
            </div>
        </section>
    )
}


export default Skills;