import style from "./Projects.module.scss";
import React from "react";
import {initialState} from "../initialState";
import Project from "./Project/Project";
import {Header} from "../common/header/Header";
import commonStyle from ".././common/styles/Container.module.scss"

const Projects = () => {
    const arrProjects = initialState.projects
    return (
        <section>
            <div className={commonStyle.wrapperBackground}>
                <article className={style.projectContainer }>
                    <Header nav={".Главная"} nameBlock={".Проекты"}/>
                    <div className={style.projects}>
                        {arrProjects.map(el => <Project title={el.title} description={el.description}/>)}
                    </div>
                </article>
            </div>
        </section>

    )
}

export default Projects;